import React from "react";
import { useEffect } from "react";
import "../CSS/table.css";

function Table(props) {
  const [page, setPage] = React.useState(1);
  const [pageLimit, setPageLimit] = React.useState(10);
  const [lastPage, setLastPage] = React.useState(1);
  const [reload, setReload] = React.useState(1);

  useEffect(() => {
    setLastPage(Math.ceil(props.data.length / pageLimit));
  }, []);

  useEffect(() => {
    setReload(reload + 1);
  }, []);

  const generateHead = () => {
    let heading = Object.keys(props.data[0]);
    return heading.map((each) => {
      return <th> {each} </th>;
    });
  };

  const generateRow = (data) => {
    return data.map((each) => {
      return <tr>{generateData(each)}</tr>;
    });
  };

  // const displayRow = () => {
  //   let start = 0;
  //   let end = 5;
  //   return generateRow(props.data.slice(start, end));
  // };

  const displayPage = () => {
    let start = (page - 1) * pageLimit;
    let end = start + pageLimit;
    return generateRow(props.data.slice(start, end));
  };

  const generateData = (eachData) => {
    let heading = Object.keys(props.data[0]);
    return heading.map((each) => {
      // return <td> {JSON.stringify(eachData)} </td>;
      return <td> {eachData[each]} </td>;
    });
  };

  const generateButtons = () => {
    let buttons = [];
    for (let i = 1; i <= lastPage; i++) {
      buttons.push(i);
    }
    return buttons.map((each) => (
      <button
        onClick={() => setPage(each)}
        style={{ backgroundColor: each === page ? "red" : "" }}
      >
        {each}
      </button>
    ));
  };

  return (
    <div>
      {/* <h2> table {props.data.length} </h2> */}
      <div className="Pagination">
        <button onClick={() => setPage(1)} disabled={page === 1}>
          &lt;&lt;
        </button>
        <button onClick={() => setPage(page - 1)} disabled={page === 1}>
          &lt;
        </button>
        <span>{reload % 2 === 0 ? generateButtons() : generateButtons()}</span>

        {/* <button onClick={() => setPage(1)}>1</button>
        <button onClick={() => setPage(2)}>2</button>
        <button onClick={() => setPage(3)}>3</button>
        <button onClick={() => setPage(4)}>4</button>
        <button onClick={() => setPage(5)}>5</button>
        <button onClick={() => setPage(6)}>6</button> */}

        <button onClick={() => setPage(page + 1)} disabled={page === lastPage}>&gt;</button>
        <button onClick={() => setPage(lastPage)} disabled={page === lastPage}>&gt;&gt;</button>
      </div>

      <table className="Labourtable">
        <thead>
          <tr>{generateHead()}</tr>
        </thead>
        <tbody>{displayPage()}</tbody>
      </table>
    </div>
  );
}
export default Table;
