import { useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import "../CSS/labourdetails.css";

function Labourdetails() {
  const labourData = useSelector((state) => state.labour.value);
  const navigate = useNavigate();
  const back = () => {
    navigate('/labourlist');
  }
  return (
    // <div>
    //   <p>Name:{labourData.Name}</p>
    // </div>
    <div className="Labourcontainer">
      <div className="Labourcard">
        <div className="Cardimage">
          <img
            alt="labour"
            src="https://cdn1.iconfinder.com/data/icons/avatars-1-5/136/1-512.png"
            width="180px"
            height="190px"
          />
        </div>

        <div className="Carddetails">
          <div>
            <h3>{labourData.Name}</h3>
            <p>{labourData.EmployeeID}</p>
          </div>
           {/* <Link to ="/labourlist"> <button> Back </button> </Link> */}
           <button onClick={() => back()}>Back</button>
        </div>
      </div>

      <div className="Labourdetails">
        <h1> Labour Details </h1>
        <table className="Labourtable">
          <tr>
            <th>Name</th>
            <td>{labourData.Name}</td>
          </tr>

          <tr>
            <th>Age</th>
            <td>{labourData.Age}</td>
          </tr>

          <tr>
            <th>Email</th>
            <td>{labourData.Email}</td>
          </tr>

          <tr>
            <th>Street</th>
            <td>{labourData.Street}</td>
          </tr>

          <tr>
            <th>City</th>
            <td>{labourData.City}</td>
          </tr>

          <tr>
            <th>Zip</th>
            <td>{labourData.Zip}</td>
          </tr>

          <tr>
            <th>Region</th>
            <td>{labourData.Region}</td>
          </tr>

          <tr>
            <th>Country</th>
            <td>{labourData.Country}</td>
          </tr>

          <tr>
            <th>Info</th>
            <td>{labourData.Info}</td>
          </tr>

          <tr>
            <th>EmployeeID</th>
            <td>{labourData.EmployeeID}</td>
          </tr>
        </table>
      </div>
    </div>
  );
}
export default Labourdetails;
