import "../CSS/login.css"
import Swal from 'sweetalert2'

function Login() {

    function loginfn() {
       // alert('message');
       Swal.fire('message')
       //fetch("http://localhost:3001/comments")
        fetch('http://localhost:3001/users/login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ userName: 'JaiGanesh@sample', password: 'jai215' })
        }).then(res => res.json())
            .then(data => {
                console.log(data);
                alert(data.data.message)
            })
    }

    return (
        <div>
            <div className="login">
                
                <h1>Login Form</h1>

                <label className="label"> <i className="fa-regular fa-user"></i> Username: </label>
                <input type="text" placeholder="Enter Username" className="input" /><br />

                <label className="label"> <i className="fa-solid fa-lock" ></i> Password: </label>
                <input type="password" placeholder="Enter password" className="input" /><br />

                <button onClick={() => loginfn()} className="btn"> Login</button>

            </div>
        </div>
    )
}

export default Login;
