import "../CSS/labourcard.css";
function Labourcard(Props) {
  return (
    <div className="Labourcard">
      {/* <div className="Cardheader">
             <h2>Labour Card</h2>
            </div> */}

      <div className="Cardimage">
        <img
          alt="Message"
          src="https://cdn1.iconfinder.com/data/icons/avatars-1-5/136/1-512.png"
          width="180px"
          height="150px"
        />
      </div>

      <div className="Carddetails">
        <div>
          <h3>{Props.Name}</h3>
          <p>{Props.ID}</p>
        </div>
        <button onClick={Props.view}>
          View <i className="fa-solid fa-eye"></i>
        </button>
      </div>
    </div>
  );
}
export default Labourcard;
