import { createSlice } from "@reduxjs/toolkit";

export const calculatorSlice = createSlice({
  name: "calculator",
  initialState: {
    calctxt: "0",
  },
  reducers: {
    clear: (state) => {
      let splittedString = state.calctxt.split("");
      console.log(splittedString);
      // state.calctxt =
      //   splittedString.length >= 1
      //     ? splittedString.pop().join("")
      //     : splittedString.join("");
      if (splittedString.length >= 1) splittedString.pop();
      state.calctxt = splittedString.join("");
    },
    allClear: (state) => {
      state.calctxt = "";
    },
    addText: (state, action) => {
      state.calctxt += action.payload;
    },
    solve: (state) => {
      state.calctxt = eval(state.calctxt);
    },
  },
});

export const { clear, allClear, addText, solve } = calculatorSlice.actions;
export default calculatorSlice.reducer;
