import "../CSS/signup.css"
function SignUp() {
    const signupfn = () => {
        alert('message');
        fetch('https://jsonplaceholder.typicode.com/posts/1', {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ username: 'Jai Ganesh', password: 'ganesh@123' })
        }).then(res => res.json())
            .then(data => console.log(data))
    }
    return (
        <div>
            <div className="container">
                <h1 className="H1"> Sign Up </h1>
                <form className="form">
                    <div className="formCover">
                        <div> First Name </div>
                        <input type="text" placeholder="First Name" />
                    </div>
                    <div className="formCover">
                        <div> Last Name </div>
                        <input type="text" placeholder="Last Name" />
                    </div>
                    <div className="formCover">
                        <div> <i className="fa-regular fa-envelope"></i> Email </div>
                        <input type="text" placeholder="Email" />
                    </div>
                    <div className="formCover">
                        <div> <i className="fa-solid fa-lock"></i> Password </div>
                        <input type="password" placeholder="Password" />
                    </div>
                    <div className="formCover">
                        <div> <i className="fa-solid fa-lock"></i> Confirm Password </div>
                        <input type="password" placeholder="Confirm password" />
                    </div>
                    <div className="sumbitButtomCover">
                        <button type="button" onClick={() => signupfn()} className="btn"> Submit </button>
                    </div>

                </form>
            </div>
        </div>
    )
}

export default SignUp;

