import Swal from "sweetalert2";
import "../CSS/empcard.css"

function Empcard() {

    const empcardfn = () => {
        fetch('https://jsonplaceholder.typicode.com/comments/3', {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json'
            }
            
        }).then(res => res.json())
            .then(data => {
                Swal.fire('Data deleted successfully')
                console.log(data)
            })
    }


    return (
        <div>
            <div className="card">

                <div className="image">
                    <img src="https://cdn.pixabay.com/photo/2017/01/31/21/23/avatar-2027366__340.png" alt="Employee"/>
                </div>

                <div className="empinfo">
                    <div className="title">
                        <h2> Jai Ganesh </h2>
                        <h2> 001 </h2>
                    </div>

                    <div className="button">
                        <button onClick={() => empcardfn()} className="btn"> <i className="fa-solid fa-eye"></i> View </button>
                    </div>

                </div>

            </div>
        </div>
    )
}
export default Empcard;