import "../CSS/chat.css"
import { useState } from 'react';
import Labourcard from "../component/labourcard";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
function Chat() {

    const [message, setMessage] = useState('message')
    const count = useSelector((state) => state.counter.value)

    return (
        <div>
            <div className='chat'>
                <h2> <i className="fa-brands fa-rocketchat"></i> Chat Message </h2>
                <button onClick={() => setMessage('hi Welcome')}> hi </button>
                <label> {message} </label>
                <button onClick={() => setMessage('Bye have a good day')}> bye </button>
                <Labourcard Name="Jai Ganesh" ID="100">
                </Labourcard>
                <Link to ="/labourlist"><i className="fa-solid fa-user-lock"></i> LabourList </Link>
                <p> count value is {count} </p>
            </div>

        </div>
    )
}
export default Chat;