import "../CSS/employeeage.css"
import React, { Component } from "react";

class empage extends Component {
    state = {age: 25};
    changeAge() {
        if (this.state.age === 25) {
            this.setState({ age: 45 });
        } else {
            this.setState({ age: 25 });
        }
        //return alert(this.age);
        //return this.age  
    }

    render() {
        return (
            <div className="agechange">
                <button onClick={() => this.changeAge()} className="btn">Change Age</button>
                <table className="agetable">
                    <tr>
                        <th> Name </th>
                        <th> Age </th>
                    </tr>
                    <tr>
                        <td>
                            Ganesh
                        </td>
                        <td id="newage">
                            {this.state.age}
                        </td>
                    </tr>
                </table>
            </div>
        )
    }
}
export default empage;