import { useDispatch, useSelector } from "react-redux";
import Swal from "sweetalert2";
import "../CSS/calculator.css";
import { allClear, clear, addText, solve } from "../store/reducer/calculator.reducer";

function Calculator() {
  const calctxt = useSelector((state) => state.calculator.calctxt);
  const dispatch = useDispatch();

  function Putfn() {
    Swal.fire("*** Data Updated ! ***");
    fetch("https://jsonplaceholder.typicode.com/posts/1", {
      method: "PUT",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify({ username: "jaiganesh", password: "GAnesh@12" }),
    })
      .then((res) => res.json())
      .then((data) => console.log(data));
  }

  function Postfn() {
    Swal.fire("*** Data Posted! ***");
    fetch("https://jsonplaceholder.typicode.com/comments", {
      method: "POST",
      headers: { "content-type": "application/json" },
      body: JSON.stringify({ empname: "Harish", empid: "002" }),
    })
      .then((res) => res.json())
      .then((data) => console.log(data));
  }

  function Deletefn() {
    Swal.fire("* * * Data Deleted * * *");
    fetch("https://jsonplaceholder.typicode.com/albums/1", {
      method: "DELETE",
      headers: {
        "content-type": "application/json",
      },
    })
      .then((res) => res.json())
      .then((data) => console.log(data));
  }

  return (
    <div>
      <div className="caldata">
        <h1>
          <i className="fa-solid fa-calculator"></i> Calculator
        </h1>
        <div>
          <div className="calcontainer">
            <input type="text" className="caltxt" value={calctxt} />
          </div>
          <div className="calbtn">
            <span>
              <button onClick={() => dispatch(addText("1"))}> 1 </button>
            </span>
            <span>
              <button onClick={() => dispatch(addText("2"))}> 2 </button>
            </span>
            <span>
              <button onClick={() => dispatch(addText("3"))}> 3 </button>
            </span>
            <span>
              <button onClick={() => dispatch(addText("/"))}> / </button>
            </span>
          </div>
          <div className="calbtn">
            <span>
              <button onClick={() => dispatch(addText("4"))}> 4 </button>
            </span>
            <span>
              <button onClick={() => dispatch(addText("5"))}> 5 </button>
            </span>
            <span>
              <button onClick={() => dispatch(addText("6"))}> 6 </button>
            </span>
            <span>
              <button onClick={() => dispatch(addText("*"))}> * </button>
            </span>
          </div>
          <div className="calbtn">
            <span>
              <button onClick={() => dispatch(addText("7"))}> 7 </button>
            </span>
            <span>
              <button onClick={() => dispatch(addText("8"))}> 8 </button>
            </span>
            <span>
              <button onClick={() => dispatch(addText("9"))}> 9 </button>
            </span>
            <span>
              <button onClick={() => dispatch(addText("-"))}> - </button>
            </span>
          </div>
          <div className="calbtn">
            <span>
              <button onClick={() => dispatch(addText("0"))}> 0 </button>
            </span>
            <span>
              <button onClick={() => dispatch(addText("."))}> . </button>
            </span>
            <span>
              <button onClick={() => dispatch(solve())}> = </button>
            </span>
            <span>
              <button onClick={() => dispatch(addText("+"))}> + </button>
            </span>
          </div>
          <div className="calbtn">
            <span>
              <button onClick={() => dispatch(clear())}> C </button>
            </span>
            <span>
              <button onClick={() => dispatch(allClear())}> AC </button>
            </span>
          </div>
        </div>
      </div>
      <div className="crud">
        <span>
          <button onClick={() => Putfn()} className="btn">
            PUT
          </button>
        </span>
        <span>
          <button onClick={() => Postfn()} className="btn">
            POST
          </button>
        </span>
        <span>
          <button onClick={() => Deletefn()} className="btn">
            DELETE
          </button>
        </span>
      </div>
      {/* <div>
        <button onClick={() => dispatch(increment())}> Add </button>
        <span>{calredux}</span>
        <button onClick={() => dispatch(decrement())}> Delete </button>
      </div> */}
    </div>
  );
}

export default Calculator;
